import { configureStore } from '@reduxjs/toolkit';
import loadingReducer from './reducers/loadingReducer/loadingReducer';
import userReducer from './reducers/userReducer/userReducer';
import addArticleReducer from './reducers/addArticleReducer/addArticleReducer';
import searchReducer from './reducers/searchReducer/searchReducer';
export default configureStore({
  reducer: {
    loading: loadingReducer,
    user: userReducer,
    addArticle: addArticleReducer,
    search: searchReducer,
  },
});
