import { createSlice } from '@reduxjs/toolkit';
import {
  login as loginApi,
  getMe,
  register as registerApi,
} from '../../../api/WebApi';
import { setAuthToken } from '../../../utils';

export const userReducer = createSlice({
  name: 'user',
  initialState: {
    user: null,
    errorMessage: null,
  },
  reducers: {
    setUser: (state, action) => {
      state.user = action.payload;
    },
    setErrorMessage: (state, action) => {
      state.errorMessage = action.payload;
    },
  },
});

export const { setUser, setErrorMessage } = userReducer.actions;

// 身分驗證
export const getUser = () => (dispatch) => {
  dispatch(setErrorMessage(null));
  return getMe().then((res) => {
    dispatch(setErrorMessage(null));
    if (res.ok !== 1) {
      setAuthToken(null);
    }
    dispatch(setUser(res.data));
  });
};

//登入
export const login = (username, password) => (dispatch) => {
  return loginApi(username, password).then((res) => {
    dispatch(setErrorMessage(null));
    if (res.ok === 0) {
      dispatch(setErrorMessage(res.message));
      return false;
    }
    setAuthToken(res.token); //存進 localstorage
    dispatch(getUser());
    return res;
  });
};

// 註冊
export const register = (username, nickname, password) => (dispatch) => {
  return registerApi(username, nickname, password).then((res) => {
    console.log(res);
    dispatch(setErrorMessage(null));
    if (res.ok === 0) {
      dispatch(setErrorMessage('帳號已被註冊'));
      return false;
    }
    if (res.ok !== 1) {
      dispatch(setErrorMessage(res.message));
      return false;
    }
    return res;
  });
};

export default userReducer.reducer;
