import { useEffect, useState } from 'react';
import styled from 'styled-components/macro';
import { MainLayout } from '../../layouts/MainLayout';
import { getArticles } from '../../api/WebApi';
import { useParams } from 'react-router-dom';
import { CategoryArticle } from '../../components';
import { setIsLoading } from '../../redux/reducers/loadingReducer';
import { useDispatch, useSelector } from 'react-redux';
import { Loading } from '../../components';
const SearchContainer = styled.div`
  display: flex;
  flex-direction: column;
  max-width: 960px;
  margin-top: 30px;
  margin-bottom: 30px;
  margin-left: auto;
  margin-right: auto;
  @media (max-width: 960px) {
    padding: 30px;
  }
`;
const SearchHead = styled.div`
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  span {
    font-size: 22px;
    margin-left: 10px;
    font-weight: bold;
  }
`;
const SearchBody = styled.div``;
const EmptyData = styled.div`
  font-size: 22px;
  color: teal;
  text-align: center;
  min-height: calc(100vh - 206px);
`;

export const SearchPage = () => {
  const [articles, setArticles] = useState([]);
  const dispatch = useDispatch();
  const isLoading = useSelector((store) => store.loading.isLoading);
  let { keyword } = useParams();

  useEffect(() => {
    dispatch(setIsLoading(true));

    getArticles()
      .then((res) => res.json())
      .then((data) => {
        const results = data.filter(({ title }) =>
          title.toLowerCase().includes(keyword)
        );
        setArticles(results);
        dispatch(setIsLoading(false));
      });

    setIsLoading(false);
  }, [dispatch, keyword]);

  if (isLoading) {
    return <Loading />;
  }
  return (
    <MainLayout>
      <SearchContainer>
        {articles.length === 0 ? (
          <>
            <EmptyData>目前無符合的資料。</EmptyData>
          </>
        ) : (
          <>
            <SearchHead>{`以下是與「${keyword}」相符的文章`}</SearchHead>
            <SearchBody>
              {articles.map((article) => (
                <CategoryArticle
                  key={article.id}
                  article={article}
                  notHasImage={!article.image}
                />
              ))}
            </SearchBody>
          </>
        )}
      </SearchContainer>
    </MainLayout>
  );
};
