import styled from 'styled-components/macro';
import { MainLayout } from '../../layouts/MainLayout';
import { setIsLoading } from '../../redux/reducers/loadingReducer';
import { getAuthorArticles, deleteArticle } from '../../api/WebApi';
import { getAuthToken } from '../../utils';
import { Loading } from '../../components/Loading';
import { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Link, useHistory } from 'react-router-dom';

const ManagementContainer = styled.div`
  display: flex;
  flex-direction: column;
  max-width: 960px;
  margin-top: 30px;
  margin-bottom: 30px;
  margin-left: auto;
  margin-right: auto;
  @media (max-width: 960px) {
    /* padding: 20px; */
  }
`;
const ManagementHead = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  .width01 {
    width: 20%;
  }
  .title {
    letter-spacing: 0.3em;
    font-size: 20px;
    font-weight: bold;
  }
  .add_article {
    width: 20%;
    text-align: right;
    font-size: 16px;
    margin-right: 10px;
    @media (max-width: 414px) {
      font-size: 14px;
      width: 30%;
    }
    .add_article_link {
      background-color: teal;
      text-decoration: none;
      border-radius: 5px;
      color: #ffffff;
      padding: 8px 15px;
    }
  }
`;
const ManagementBody = styled.div`
  margin: 20px 0;
  padding: 25px 40px;
  width: 100%;
  border-radius: 10px;
  box-shadow: rgb(33 83 154 / 8%) 0px 0px 30px,
    rgb(255 255 255 / 30%) 1px 1px 5px inset;
  transition: all 0.3s ease 0s;
  background-color: rgba(255, 255, 255, 0.3);
  .management_box + .management_box {
    margin: 10px 0;
  }
  .management_box {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid rgb(217, 217, 217);
    padding: 15px;
    :hover {
      background-color: #f0f0f0;
    }

    .date {
      @media (max-width: 414px) {
        font-size: 14px;
      }
    }
    .title {
      color: #333;
      text-decoration: none;
      :hover {
        color: teal;
      }
      @media (max-width: 768px) {
        font-size: 14px;
      }
    }
    .control_btn {
      display: flex;
      .edit_btn {
        margin-right: 10px;
        color: teal;
        cursor: pointer;
        text-decoration: none;
      }
      .delete_btn {
        cursor: pointer;
      }
    }
  }
`;

export const ArticlesManagementPage = () => {
  const [articles, setArticles] = useState([]);

  const history = useHistory();
  const dispatch = useDispatch();
  const user = useSelector((store) => store.user.user);
  const isLoading = useSelector((store) => store.loading.isLoading);

  const handelDeleteArticle = (e) => {
    e.preventDefault();
    console.log(e.target.dataset);
    deleteArticle(e.target.dataset.articleId).then((res) => {
      history.go(0);
    });
  };

  useEffect(() => {
    dispatch(setIsLoading(true));
    if (!getAuthToken() && !user) {
      history.push('/login');
    }
    if (user) {
      getAuthorArticles(user.id).then((res) => {
        setArticles(res);
        dispatch(setIsLoading(false));
      });
    }
  }, [user, history, dispatch]);

  if (isLoading) {
    return <Loading />;
  }
  return (
    <MainLayout>
      <ManagementContainer>
        <ManagementHead>
          <div className="width01"></div>
          <div className="title">文章管理</div>
          <div className="add_article">
            <Link to="/add-article" className="add_article_link">
              新增文章
            </Link>
          </div>
        </ManagementHead>
        <ManagementBody>
          {articles.map((article) => {
            return (
              <div className="management_box" key={article.id}>
                <div className="date">
                  {new Date(article.createdAt).toLocaleString()}
                </div>
                <Link to={`/article/${article.id}`} className="title">
                  {article.title}
                </Link>
                <div className="control_btn">
                  <Link to={`/edit/${article.id}`} className="edit_btn">
                    編輯
                  </Link>
                  <div
                    className="delete_btn"
                    onClick={handelDeleteArticle}
                    data-article-id={article.id}
                  >
                    刪除
                  </div>
                </div>
              </div>
            );
          })}
        </ManagementBody>
      </ManagementContainer>
    </MainLayout>
  );
};
