import { getAuthToken } from '../utils';

const BASE_URL = 'https://lidemy-api.herokuapp.com/';

// 獲取所有文章
export const getArticles = (page, limit) => {
  return fetch(
    `${BASE_URL}/articles?_sort=createdAt&_order=desc&_expand=user&_page=${page}&_limit=${limit}`
  );
};

// 獲取單篇文章
export const getSingleArticle = (id) => {
  return fetch(`${BASE_URL}/articles/${id}?_expand=user`).then((res) =>
    res.json()
  );
};

// 獲取所有分類
export const getCategories = () => {
  return fetch(`${BASE_URL}/categories`).then((res) => res.json());
};

// 獲取所有分類文章
export const getAllCategoryArticles = (category) => {
  return fetch(
    `${BASE_URL}/articles?_sort=createdAt&_order=desc&_expand=user&category=${category}`
  ).then((res) => res.json());
};

// 獲取分類文章(limit)
export const getCategoryArticles = (page, limit, category) => {
  return fetch(
    `${BASE_URL}/articles?_sort=createdAt&_order=desc&_expand=user&_page=${page}&_limit=${limit}&category=${category}`
  );
};

// 獲取指定分類
export const getAssignCategory = (category) => {
  return fetch(`${BASE_URL}/categories?name=${category}`).then((res) =>
    res.json()
  );
};

// 獲取 user 發佈的文章
export const getAuthorArticles = (userId) => {
  return fetch(
    `${BASE_URL}/articles?userId=${userId}&_sort=createdAt&_order=desc&_expand=user`
  ).then((res) => res.json());
};

// 登入
export const login = (username, password) => {
  return fetch(`${BASE_URL}/login`, {
    method: 'POST',
    headers: {
      'content-type': 'application/json',
    },
    body: JSON.stringify({
      username,
      password,
    }),
  }).then((res) => res.json());
};

//身分驗證
export const getMe = () => {
  const token = getAuthToken();
  return fetch(`${BASE_URL}/me`, {
    headers: {
      authorization: `Bearer ${token}`,
    },
  }).then((res) => res.json());
};

//註冊
export const register = (username, nickname, password) => {
  return fetch(`${BASE_URL}/register`, {
    method: 'POST',
    headers: {
      'content-type': 'application/json',
    },
    body: JSON.stringify({
      username,
      nickname,
      password,
    }),
  }).then((res) => res.json());
};

//新增文章
export const addArticle = (title, category, image, content) => {
  const token = getAuthToken();
  return fetch(`${BASE_URL}/articles`, {
    method: 'POST',
    headers: {
      authorization: `Bearer ${token}`,
      'content-type': 'application/json',
    },
    body: JSON.stringify({
      title,
      category,
      image,
      body: content,
    }),
  }).then((res) => res.json());
};

//刪除文章
export const deleteArticle = (id) => {
  return fetch(`${BASE_URL}/articles/${id}`, {
    method: 'DELETE',
  }).then((res) => res.json());
};

//編輯文章
export const updateArticle = (id, title, category, image, content) => {
  const token = getAuthToken();
  return fetch(`${BASE_URL}/articles/${id}`, {
    method: 'PATCH',
    headers: {
      authorization: `Bearer ${token}`,
      'content-type': 'application/json',
    },
    body: JSON.stringify({
      title,
      category,
      image,
      body: content,
    }),
  }).then((res) => res.json());
};
