import { MainLayout } from '../../layouts/MainLayout';
import styled from 'styled-components/macro';
import { AiOutlineUser, AiFillEye, AiFillEyeInvisible } from 'react-icons/ai';
import { RiLockPasswordLine } from 'react-icons/ri';
import { ImNotification } from 'react-icons/im';
import { useState } from 'react';
import { useHistory } from 'react-router-dom';
import { setErrorMessage, login } from '../../redux/reducers/userReducer';
import { useDispatch, useSelector } from 'react-redux';

const LoginPageContainer = styled.div`
  max-width: 400px;
  margin: 150px auto;
`;
const TabContainer = styled.div`
  display: flex;
  align-items: center;
  .login {
    padding: 15px 0;
    width: 50%;
    border-radius: 10px 0 0 0;
    background-color: #fff;
    color: black;
    text-align: center;
  }
  .register {
    padding: 15px 0;
    width: 50%;
    border-radius: 10px 0 0 0;
    background-color: rgb(162, 181, 159, 0.4);
    color: black;
    text-align: center;
    cursor: pointer;
  }
`;
const FormContainer = styled.form`
  margin-bottom: 20px;
  padding: 20px 0;
  background-color: #fff;

  .input_container {
    position: relative;
    padding: 10px 30px;
    margin: 10px 0;
    .user_input {
      width: 100%;
      padding: 5px 10px 5px 40px;
      height: 45px;
      border: solid 1px transparent;
      border-radius: 3px;
      font-size: 16px;
      color: #333;
      background-color: #a2b59f;
      transition: 0.3s;
      ::placeholder {
        color: #fff;
        opacity: 0.5;
      }
    }
    .user_icon {
      position: absolute;
      left: 10%;
      top: 50%;
      transform: translateY(-50%);
      font-size: 18px;
    }

    .password_input {
      position: relative;
      width: 100%;
      padding: 5px 10px 5px 40px;
      height: 45px;
      border: solid 1px transparent;
      border-radius: 3px;
      font-size: 16px;
      color: #333;
      background-color: #a2b59f;
      transition: 0.3s;
      ::placeholder {
        color: #fff;
        opacity: 0.5;
      }
    }
    .password_icon {
      position: absolute;
      left: 10%;
      top: 50%;
      transform: translateY(-50%);
      font-size: 18px;
    }
    .show_password_icon {
      cursor: pointer;
      position: absolute;
      right: 10%;
      top: 50%;
      transform: translateY(-50%);
      font-size: 18px;
    }
  }
  .notice {
    margin-bottom: 10px;
    padding: 5px 10px 5px 40px;
    color: #333;
    span {
      margin-left: 10px;
    }
  }
  .submit {
    margin: 10px 30px;
    .submit_button {
      cursor: pointer;
      width: 100%;
      padding: 12px 0;
      text-align: center;
      letter-spacing: 0.3em;
      color: #fff;
      background-color: teal;
      border: transparent;
      border-radius: 3px;
      font-size: 16px;
      cursor: pointer;
      transition: 0.3s;
      &:hover {
        opacity: 0.9;
      }
    }
    .error_message {
      text-align: center;
      color: red;
      margin: 10px 0;
    }
  }
`;
export const LoginPage = () => {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('Lidemy');
  const [isShowPassword, setIsShowPassword] = useState(false);

  const history = useHistory();
  const dispatch = useDispatch();
  const errorMessage = useSelector((store) => store.user.errorMessage);

  //切換登入頁面
  const handleChangeTabLogin = () => {
    history.push('/login');
  };

  //切換註冊頁面
  const handleChangeTabRegister = () => {
    history.push('/register');
  };

  // 點擊顯示密碼
  const handleShowPassword = () => {
    setIsShowPassword(!isShowPassword);
  };

  // 綁定 username change
  const handleSetUsername = (e) => {
    setUsername(e.target.value);
  };

  // 綁定 password change
  const handleSetPassword = (e) => {
    setPassword(e.target.value);
  };

  // 登入表單送出
  const handleSubmit = (e) => {
    e.preventDefault();
    dispatch(setErrorMessage(null));

    //判斷欄位是否為空
    if (!username || !password) {
      e.preventDefault();
      return dispatch(setErrorMessage('username or password unfilled'));
    }

    dispatch(login(username, password)).then((res) => {
      if (res) {
        history.push('/');
      }
    });
  };

  return (
    <MainLayout>
      <LoginPageContainer>
        <TabContainer>
          <div className="login" onClick={handleChangeTabLogin}>
            登入
          </div>
          <div className="register" onClick={handleChangeTabRegister}>
            註冊
          </div>
        </TabContainer>
        <FormContainer onSubmit={handleSubmit}>
          <div className="input_container">
            <input
              type="text"
              name="username"
              placeholder="請輸入帳號"
              className="user_input"
              onChange={handleSetUsername}
              value={username}
            />
            <AiOutlineUser className="user_icon" />
          </div>

          <div className="input_container">
            <input
              type={isShowPassword ? 'text' : 'password'}
              name="password"
              placeholder="請輸入密碼"
              className="password_input"
              onChange={handleSetPassword}
              value={password}
            />
            <RiLockPasswordLine className="password_icon" />
            <div onClick={handleShowPassword}>
              {isShowPassword ? (
                <AiFillEye className="show_password_icon" />
              ) : (
                <AiFillEyeInvisible className="show_password_icon" />
              )}
            </div>
          </div>
          <div className="notice">
            <ImNotification />
            <span>密碼設定皆為默認值</span>
          </div>
          <div className="submit">
            <button className="submit_button">登入</button>
            {errorMessage && (
              <div className="error_message">{errorMessage}</div>
            )}
          </div>
        </FormContainer>
      </LoginPageContainer>
    </MainLayout>
  );
};
