import { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import styled from 'styled-components/macro';
import { MainLayout } from '../../layouts/MainLayout';
import {
  setNewArticle,
  addArticle,
  setErrorMessage,
} from '../../redux/reducers/addArticleReducer';
import { setIsLoading } from '../../redux/reducers/loadingReducer';
import { EditArticle } from '../../components/EditArticle';
import { Loading } from '../../components';
import { getCategories } from '../../api/WebApi';
import { getAuthToken } from '../../utils';
import { useHistory } from 'react-router-dom';

const AddArticleContainer = styled.div`
  display: flex;
  flex-direction: column;
  max-width: 960px;
  margin-top: 30px;
  margin-bottom: 30px;
  margin-left: auto;
  margin-right: auto;

  .title {
    text-align: center;
    font-size: 24px;
    color: rgb(48, 48, 48);
    margin-bottom: 20px;
    font-weight: bold;
  }
`;

export const AddArticlePage = () => {
  const [articleTitle, setArticleTitle] = useState('');
  const [articleContent, setArticleContent] = useState('');
  const [articleImage, setArticleImage] = useState('');
  const [articlesCategories, setArticlesCategories] = useState([]);
  const [currentCategory, setCurrentCategory] = useState('');

  const dispatch = useDispatch();
  const history = useHistory();
  const user = useSelector((store) => store.user.user);
  const errorMessage = useSelector((store) => store.addArticle.errorMessage);
  const isLoading = useSelector((store) => store.loading.isLoading);

  const handleSubmit = (e) => {
    e.preventDefault();
    if (!articleTitle || !currentCategory || !articleContent) {
      return dispatch(setErrorMessage('資料有缺'));
    }

    dispatch(
      addArticle(articleTitle, currentCategory, articleImage, articleContent)
    ).then((res) => {
      if (res) {
        history.push('/articles');
      }
    });
  };

  useEffect(() => {
    if (!getAuthToken() && !user) {
      history.push('/login');
    }
    dispatch(setIsLoading(true));
    dispatch(setNewArticle(null));
    dispatch(setErrorMessage(null));
    getCategories().then((res) => {
      dispatch(setIsLoading(true));
      setArticlesCategories(res);
      setCurrentCategory(res[0].name);
      dispatch(setIsLoading(false));
    });
  }, [user, dispatch]);

  if (isLoading) {
    return <Loading />;
  }
  return (
    <MainLayout>
      <AddArticleContainer>
        <div className="title">新增文章</div>
        <EditArticle
          articleTitle={articleTitle}
          setArticleTitle={setArticleTitle}
          articleContent={articleContent}
          setArticleContent={setArticleContent}
          articleImage={articleImage}
          setArticleImage={setArticleImage}
          articlesCategories={articlesCategories}
          currentCategory={currentCategory}
          setCurrentCategory={setCurrentCategory}
          handleSubmit={handleSubmit}
          errorMessage={errorMessage}
        />
      </AddArticleContainer>
    </MainLayout>
  );
};
