import styled from 'styled-components/macro';
import { AiFillGithub, AiOutlineMail } from 'react-icons/ai';

const FooterContainer = styled.div`
  padding: 0 0 30px 0;
  .footer_info {
    display: flex;
    justify-content: center;
    align-items: center;
  }
`;

const GitHubIcon = styled.a`
  cursor: pointer;
  display: block;
  margin: 5px 0 0 10px;
  color: #333;
  :hover {
    color: teal;
  }
`;
const MailIcon = styled.a`
  cursor: pointer;
  display: block;
  margin: 5px 0 0 10px;
  color: #333;
  :hover {
    color: teal;
  }
`;
const Source = styled.a`
  cursor: pointer;
  display: block;
  font-size: 14px;
  text-decoration: none;
  color: #333;
  letter-spacing: 0.1em;
  margin: 10px 0 0 10px;
  :hover {
    color: teal;
  }
`;
export const Footer = () => {
  return (
    <FooterContainer>
      <div className="footer_info">
        <p>© Bryan Wang</p>
        <GitHubIcon href="https://github.com/Bryan9411" target="_blank">
          <AiFillGithub />
        </GitHubIcon>
        <MailIcon href="mailto:a9874687@gmail.com">
          <AiOutlineMail />
        </MailIcon>
      </div>
      <div className="footer_info">
        <Source
          href="https://react-icons.github.io/react-icons/"
          target="_blank"
        >
          Icons Source
        </Source>
        <Source
          href="https://github.com/Bryan9411/react-redux-blog"
          target="_blank"
        >
          Source Code
        </Source>
      </div>
    </FooterContainer>
  );
};
