import styled from 'styled-components/macro';
import { MainLayout } from '../../layouts/MainLayout';
import { EditArticle } from '../../components/EditArticle';
import {
  getSingleArticle,
  getCategories,
  updateArticle,
} from '../../api/WebApi';
import { getAuthToken } from '.././../utils';
import { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import { useHistory, useParams } from 'react-router-dom';
const EditArticleContainer = styled.div`
  display: flex;
  flex-direction: column;
  max-width: 960px;
  margin-top: 30px;
  margin-bottom: 30px;
  margin-left: auto;
  margin-right: auto;
  .title {
    text-align: center;
    font-size: 24px;
    color: rgb(48, 48, 48);
    margin-bottom: 20px;
    font-weight: bold;
  }
`;
export const EditArticlePage = () => {
  const [articlesCategories, setArticlesCategories] = useState([]);
  const [currentCategory, setCurrentCategory] = useState('');
  const [articleTitle, setArticleTitle] = useState('');
  const [articleContent, setArticleContent] = useState('');
  const [aricleImage, setArticleImage] = useState('');

  const history = useHistory();
  const user = useSelector((store) => store.user.user);
  let { id } = useParams();
  useEffect(() => {
    if (!getAuthToken() && !user) {
      history.push('/login');
    }
    getSingleArticle(id).then((data) => {
      setArticleTitle(data.title);
      setArticleContent(data.body);
      setArticleImage(data.image);
      setCurrentCategory(data.category);
    });
    getCategories().then((res) => {
      setArticlesCategories(res);
      console.log(res);
    });
  }, []);

  const handleSubmit = (e) => {
    e.preventDefault();
    if (!articleTitle || !articleContent) {
      console.log('內容有缺');
    }

    updateArticle(
      id,
      articleTitle,
      currentCategory,
      aricleImage,
      articleContent
    ).then((res) => {
      history.push(`/article/${id}`);
    });
  };
  return (
    <MainLayout>
      <EditArticleContainer>
        <div className="title">新增文章</div>
        <EditArticle
          handleSubmit={handleSubmit}
          articlesCategories={articlesCategories}
          currentCategory={currentCategory}
          setCurrentCategory={setCurrentCategory}
          articleTitle={articleTitle}
          setArticleTitle={setArticleTitle}
          articleContent={articleContent}
          setArticleContent={setArticleContent}
          aricleImage={aricleImage}
          setArticleImage={setArticleImage}
        />
      </EditArticleContainer>
    </MainLayout>
  );
};
