import styled from 'styled-components/macro';

const FormContainer = styled.form`
  display: flex;
  flex-direction: column;
  align-items: center;
  @media (max-width: 960px) {
    padding: 20px;
  }
`;
const FormHead = styled.div`
  display: flex;
  margin-bottom: 20px;
  width: 100%;
  input {
    padding: 15px;
    width: calc(100% - 165px);
    border: 1px solid transparent;
    border-radius: 3px;
    color: rgb(48, 48, 48);
    background-color: rgb(255, 255, 255);
    box-shadow: rgb(33 83 154 / 8%) 0px 0px 30px;
    transition: all 0.3s ease 0s;
    margin-right: 20px;
  }
  select {
    margin-right: 15px;
    padding: 15px;
    font-size: 16px;
    width: 150px;
    border: 1px solid transparent;
    border-radius: 3px;
    color: rgb(48, 48, 48);
    background-color: rgb(255, 255, 255);
    box-shadow: rgb(33 83 154 / 8%) 0px 0px 30px;
    option {
      width: 100%;
    }
  }
`;
const FormImage = styled.div`
  margin-bottom: 20px;
  width: 100%;
  input {
    padding: 15px;
    width: calc(100% - 15px);
    border: 1px solid transparent;
    border-radius: 3px;
    color: rgb(48, 48, 48);
    background-color: rgb(255, 255, 255);
    box-shadow: rgb(33 83 154 / 8%) 0px 0px 30px;
  }
`;
const FormBody = styled.div`
  width: 100%;
  margin-bottom: 20px;
  textarea {
    width: calc(100% - 15px);
    height: 500px;
  }
`;
const FormButton = styled.div`
  margin: auto;
  button {
    padding: 10px 40px;
    background-color: rgb(0, 108, 128);
    color: rgb(255, 255, 255);
    border: transparent;
    border-radius: 3px;
    font-size: 16px;
    cursor: pointer;
  }
  .error_message {
    text-align: center;
    color: red;
    margin: 10px 0;
    font-weight: bold;
    letter-spacing: 0.2em;
  }
`;
export const EditArticle = ({
  articleTitle,
  setArticleTitle,
  articleImage,
  setArticleImage,
  articleContent,
  setArticleContent,
  currentCategory,
  setCurrentCategory,
  articlesCategories,
  handleSubmit,
  errorMessage,
}) => {
  const handleInputChange = (e) => {
    e.preventDefault();
    switch (e.target.name) {
      case 'articleTitle':
        setArticleTitle(e.target.value);
        console.log(e.target.value);
        break;
      case 'articleImage':
        setArticleImage(e.target.value);
        console.log(articleImage);
        break;
      case 'articleContent':
        setArticleContent(e.target.value);
        break;
      default:
        break;
    }
  };

  const handleChangeCategory = (e) => {
    setCurrentCategory(e.target.value);
  };
  return (
    <FormContainer onSubmit={handleSubmit}>
      <FormHead>
        <input
          type="text"
          name="articleTitle"
          placeholder="文章標題"
          onChange={handleInputChange}
          value={articleTitle}
        />
        <select value={currentCategory} onChange={handleChangeCategory}>
          {articlesCategories.map((articleCategory) => (
            <option key={articleCategory.id} value={articleCategory.name}>
              {articleCategory.name}
            </option>
          ))}
        </select>
      </FormHead>
      <FormImage>
        <input
          type="text"
          placeholder="圖片網址"
          name="articleImage"
          onChange={handleInputChange}
          value={articleImage}
        />
      </FormImage>
      <FormBody>
        <textarea
          tyep="text"
          name="articleContent"
          placeholder="請輸入文章內容"
          onChange={handleInputChange}
          value={articleContent}
        />
      </FormBody>
      <FormButton>
        <button>送出</button>
        {errorMessage && <div className="error_message">{errorMessage}</div>}
      </FormButton>
    </FormContainer>
  );
};
