import { createSlice } from '@reduxjs/toolkit';
import { addArticle as addArticleApi } from '../../../api/WebApi';
export const addArticleReducer = createSlice({
  name: 'addArticle',
  initialState: {
    article: null,
    errorMessage: null,
  },
  reducers: {
    setNewArticle: (state, action) => {
      state.article = action.payload;
    },
    setErrorMessage: (state, action) => {
      state.errorMessage = action.payload;
    },
  },
});

export const { setNewArticle, setErrorMessage } = addArticleReducer.actions;

export const addArticle = (title, category, image, content) => (dispatch) => {
  return addArticleApi(title, category, image, content).then((res) => {
    if (res.ok === 0) {
      dispatch(setErrorMessage(res.message));
      return false;
    }
    dispatch(setNewArticle(res));
    return res;
  });
};

export default addArticleReducer.reducer;
