import { useEffect } from 'react';
import { HashRouter, Route, Switch } from 'react-router-dom';
import {
  HomePage,
  ArticlesPage,
  SingleArticlePage,
  CategoryPage,
  AuthorPage,
  LoginPage,
  RegisterPage,
  AddArticlePage,
  ArticlesManagementPage,
  EditArticlePage,
  SearchPage,
} from './pages';
import { getAuthToken } from './utils';
import { getUser } from './redux/reducers/userReducer';
import { useDispatch } from 'react-redux';

function App() {
  const dispatch = useDispatch();

  useEffect(() => {
    if (!getAuthToken) return;
    dispatch(getUser());
  }, [dispatch]);

  return (
    <HashRouter>
      <Switch>
        <Route exact path="/" component={HomePage} />
        <Route path="/articles" component={ArticlesPage} />
        <Route path="/article/:id" component={SingleArticlePage} />
        <Route path="/category/:categoryName" component={CategoryPage} />
        <Route path="/Author/:userId" component={AuthorPage} />
        <Route path="/login" component={LoginPage} />
        <Route path="/register" component={RegisterPage} />
        <Route path="/add-article" component={AddArticlePage} />
        <Route path="/articles-management" component={ArticlesManagementPage} />
        <Route path="/edit/:id" component={EditArticlePage} />
        <Route path="/search/:keyword" component={SearchPage} />
      </Switch>
    </HashRouter>
  );
}

export default App;
